import { db } from "@/lib/db";
import Link from "next/link";

export default async function MarketPage() {
  const products = await db.funnel.findMany({
    where: { published: true },
    orderBy: { createdAt: "desc" }
  });

  return (
    <div className="min-h-screen bg-slate-950 text-white p-8">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl font-bold mb-2">Nexus Marketplace</h1>
        <p className="text-slate-400 mb-8">Discover premium digital assets.</p>

        {products.length === 0 ? (
            <div className="text-center py-20 border border-slate-800 rounded-xl">
                <p className="text-slate-500">No products found. Run the seed script!</p>
            </div>
        ) : (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {products.map((p) => (
                <Link key={p.id} href={`/f/${p.id}`} className="group block h-full">
                  <div className="bg-slate-900 rounded-2xl border border-slate-800 overflow-hidden h-full flex flex-col hover:border-blue-500 transition duration-300">
                    <div className="h-48 bg-gradient-to-br from-slate-800 to-slate-900 flex items-center justify-center p-4">
                        <h3 className="text-white font-bold text-xl text-center">{p.headline[0]}</h3>
                    </div>
                    <div className="p-6">
                        <div className="flex justify-between mb-3">
                            <span className="bg-blue-900/30 text-blue-400 text-xs font-bold px-2 py-1 rounded border border-blue-900">DIGITAL</span>
                            <span className="font-bold text-emerald-400">KES {p.price}</span>
                        </div>
                        <p className="text-slate-400 line-clamp-2 text-sm">{p.description}</p>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
        )}
      </div>
    </div>
  );
}
